@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Home {
    background-color: #000;
    padding: 80px 160px;
    display: flex;
    flex-direction: column;
    max-width: 1440px;
    margin: auto;
}

h1 {
    font-family: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500;
    color: #fff;
    font-size: 24px;
    line-height: 120%;
    margin: 0;
    padding: 0;
}

h2, p, button, h3, a {
    font-family: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;
    color: #fff;
    font-size: 18px;
    line-height: 150%;
    margin: 0;
    padding: 0;
}

h2 {
    font-weight: 500;
}

a {
    text-decoration: none;
    color: #71CEEF;
}

a:visited {
    text-decoration: none;
    color: #71CEEF;
}

#secondary {
    color:#999;
}

#header {
    font-weight: 500;
    letter-spacing: 6px;
    color: #a8a8a8;
    font-feature-settings: "smcp";
    font-variant: small-caps;
    padding-bottom: 32px;
}

span {
    text-decoration: underline;
}

#highlight {
    color: #71CEEF;
}

.fade-in-section {
    opacity: 0;
    visibility: hidden;
    transition: opacity 600ms ease-in, visibility 600ms ease-out;
    will-change: opacity, transform, visibility;
}
.fade-in-section.is-visible {
    opacity: 1;
    visibility: visible;
}

/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 480px) {

    .Home {
        padding: 48px 24px;
        overflow-x: hidden;
    }

    h1 {
        font-size: 18px;
    }

    #header {
        padding-bottom: 24px;
    }
}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
        
    .Home {
        padding: 80px 80px;
    }

}
.Hero {
    width: 500px;
    display: flex;
    flex-direction: column;
    padding: 32px 24px;
}

#strikethrough {
    text-decoration: line-through;
}

.Title {
    display: flex;
    flex-direction: column;
    height: 48px;
    padding-bottom: 32px;
}

.Socials {
    display: flex;
    flex-direction: row;
    width: 128px;
    justify-content: space-between;
    height: 20px;
    padding-bottom: 32px;
    opacity: 0.6;
}

.Bio {
    padding-bottom: 32px;
}

.Socials a img {
    height: 20px;
}

/* mobile portrait */
@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {

    .Hero {
        padding: 32px 0px;
        width: 100%;
    }

    .Bio {
        width: 100%;
    }

    .Title, .Socials .Bio {
        padding-bottom:24px;
    }
    
}
.Button {
    padding: 8px;
    background-color: #121D21;
    border-radius: 4px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    transform: translate();
    transition: 300ms ease-in-out;
}

.Button:hover {
    transform: translateY(-4px) scale(1.05);
}
.hover_img  { position:relative; }
.hover_img span { position:absolute; display:none; z-index:99; margin-top:-100px; margin-left: -50px; left: 0; top: 0; opacity: 0.5;}
.hover_img span img {width: 200px; height: auto;}
.hover_img:hover span { display:block; }

button {
    border:none;
    background-color: transparent;
    color: #71CEEF;
}
.Projects {
    padding: 32px 24px;
    margin-bottom: 32px;
}


/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 1024px) {

    .Projects {
        padding: 32px 0px;
        margin-bottom: 12px;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) {
    .Projects {
        padding: 32px 24px;
    }
}
.Post {
    padding-bottom: 128px;
}

.Post > img {
    width: 100%;
    height: 500px;
    object-fit: cover;
    object-position: center;
    border-radius: 8px;
    margin-bottom: 32px;
}

.ContentContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;;
}

.ContentContainer .RightPane {
    width: 500px;
}

.ContentContainer .LeftPane {
    width: 450px;
}

.Logos {
    padding-top: 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.publication {
    height: 18px;
    margin-right: 18px;
    width: auto;
}

#Bootcamp, #Muzli {
    height: 18px;
}

#uxc {
    height:24px;
}

/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 1024px) {

    .Post {
        padding-bottom: 64px;
    }

    .Post > img {
        width: 100%;
        height: 200px;
    }

    .ContentContainer {
        flex-direction: column;
    }

    .ContentContainer .RightPane, .ContentContainer .LeftPane {
        width: 100%;
    }

    .Logos {
        padding-top: 12px;
    }

    .ContentContainer .RightPane {
        margin-top: 12px;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {

    .Post > img {
        height: 400px;
    }
        
    .ContentContainer {
        width: 500px
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {

    .Post > img {
        height: 300px;
        margin-bottom: 16px;
    }

}
.TestimonialContainer {
    display:flex;
    flex-direction: row;
    margin-bottom: 32px;
}

.Testimonial {
    padding: 24px 32px;
}

.TestimonialPost {
    margin-right: 32px;
    width: 400px;
    padding: 24px 32px;
    border-radius: 8px;
}

.TestimonialPost .Author {
    /* display: flex;
    flex-direction: row;
    align-items: center; */
    margin-top: 24px;
}

.TestimonialPost .Author a {
    font-weight: 600;
}

.TestimonialPost #twitter {
    height: 18px;
    width: auto;
}

#first p, #first .Author a, #first .Author #twitter {
    color: #E0A685;
    fill: #E0A685;
}

#twitter {
    fill: yellow;
}

#second p, #second .Author a, #second #twitter {
    color: #A8CDA2;
}

#first {
    background-color: #150B05;
}

#second {
    background-color: #0C0E0B;
}

/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 1024px) {

    .TestimonialContainer {
        flex-direction: column;
        margin-bottom: 12px;
    }

    .Testimonial {
        padding: 32px 0px;
    }

    .TestimonialPost {
        margin-top: 16px;
        margin-right: 0px;
        width: auto;
        padding: 16px 24px;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
        
    .Testimonial {
        padding: 32px 24px;
        /* margin-top: -64px; */
    }

    .TestimonialPost {
        width: 500px;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
        
    .Testimonial {
        padding: 32px 24px;
        /* margin-top: -64px; */
    }

    .TestimonialContainer {
        max-width: 500px;
    }

}
.Contact {
    width: 500px;
    padding: 32px 24px;
}

.Contact #text {
    margin-bottom: 32px;
}

.ButtonContainer {
    display: flex;
    margin-bottom: 32px;
    flex-direction: row;
}

.ButtonContainer div {
    margin-right: 24px;
}

@media (min-device-width : 320px) and (max-device-width : 480px) {

    .Contact {
        width: 100%;
        padding: 32px 0px;
    }

    .Contact #text {
        margin-bottom: 12px;
    }

    .ButtonContainer {
        flex-direction: column;
    }

    .ButtonContainer div {
        margin-right: 0px;
        margin-top: 24px;
    }

}
.Skills {
    width: 500px;
    display: flex;
    flex-direction: column;
    padding: 32px 24px;
    margin-top: -96px;
}

.Skills #focus {
    color: white;
    text-decoration: none;
}

@media (min-device-width : 320px) and (max-device-width : 480px) {

    .Skills {
        padding: 32px 0px 0px 0px;
        width: 100%;
        margin-bottom: 24px;
    }
    
}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
        
    .Skills {
        padding: 32px 24px;
        margin-top: -64px;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
        
    .Skills {
        padding: 32px 24px;
        margin-top: -64px;
    }
    
}
